import React from "react"
import Layout from "../components/layout"
import SEO from "../components/seo"


const IndexPage = () => (
  <Layout>
    <SEO title="Home" />
    <article>
        <img className={"person_photo"} src={'../../bida/bida_2.jpg'} alt={""} />
        <div>
          <div className={"person_name"}>Александра Бида</div>
        </div>
      <div className={"textBlock"}>
          <p>Сидеть дома мне не&nbsp;очень сложно. Возможно, это связано с&nbsp;тем, что я&nbsp;выхожу периодически на&nbsp;улицу, чтобы погулять с&nbsp;собакой. Для себя я&nbsp;нахожу как плюсы, так и&nbsp;минусы ситуации, которая сложилась в&nbsp;нашей стране. Из&nbsp;плюсов могу выделить появление большого количества свободного времени, возможность распоряжаться, расставлять свои приоритеты. Если говорить о&nbsp;минусах, то&nbsp;это невозможность выйти из&nbsp;дома, встретиться с&nbsp;друзьями, погулять. Я&nbsp;считаю себя экстравертом и&nbsp;не&nbsp;могу без всего вышеперечисленного, но&nbsp;приходиться смириться с&nbsp;этим и&nbsp;придумывать новые способы общения с&nbsp;друзьями, например, видеосвязь в&nbsp;различных социальных сетях. Я&nbsp;предпочитаю WhatsApp. Я&nbsp;очень много общаюсь со&nbsp;своими друзьями. Мы&nbsp;созваниваемся между уроками, даже на&nbsp;15&nbsp;минут, мы&nbsp;говорим на&nbsp;уроках, постоянно встречаемся и&nbsp;общаемся в&nbsp;режиме онлайн, например, в&nbsp;социальных сетях, в&nbsp;видеозвонках, в&nbsp;обычных звонках. В&nbsp;основном мы&nbsp;общаемся по&nbsp;видеозвонкам&nbsp;&mdash; это самый комфортный вид связи. Видеозвонки, по&nbsp;моему мнению, создают некое ощущение присутствия. Когда я&nbsp;общаюсь с&nbsp;кем-то из&nbsp;своих друзей по&nbsp;видео, я&nbsp;могу наблюдать за&nbsp;ним, помогать ему в&nbsp;чём-то, видеть его в&nbsp;реальном времени. Однажды, когда мы&nbsp;разговаривали с&nbsp;моей подругой, к&nbsp;нам в&nbsp;комнаты синхронно ворвались наши младшие сёстры. Это было очень забавно и&nbsp;неожиданно.</p>
          <p>Не&nbsp;могу сказать, что нравлюсь себе на&nbsp;экране своего девайса. Скорее нет, чем&nbsp;да. Это связано с&nbsp;тем, что камера моего компьютера не&nbsp;передаёт изображение в&nbsp;нормальном качестве, и&nbsp;мое лицо выглядит очень опухшим, это так забавно))</p>
          <p>Я&nbsp;не&nbsp;скажу, что научилась чему-то новому за&nbsp;это время, но&nbsp;у&nbsp;меня появилось больше времени на&nbsp;любимое дело. Я&nbsp;люблю готовить и&nbsp;стала заниматься этим все свое свободное время, это меня радует. За&nbsp;карантин я&nbsp;успела приготовить множество выпечки, различных мясных блюд и&nbsp;закусок. Также я&nbsp;возобновила свои занятия на&nbsp;ремнях TPX. Это очень удобный тренажёр, который можно использовать дома, чтобы поддерживать свою форму.</p>
          <p>Читаю я&nbsp;в&nbsp;основном книги по&nbsp;школьной программе, посмотрела много разных фильмов и&nbsp;сериалов. Могу посоветовать: &laquo;Богемская рапсодия&raquo;, &laquo;Мушкетёр&raquo;, &laquo;CSI: NY&raquo;. Хотя думаю, что у&nbsp;всех свои вкусы и&nbsp;каждому нравится своё.</p>
          <img className={"text_photo"} src={'../../bida/bida_1.jpg'} alt={""} />
          <p>К&nbsp;сожалению, мы&nbsp;сидим на&nbsp;карантине не&nbsp;всей семьей, что связано с&nbsp;профессией моих родителей, поэтому никаких новых привычек или традиций у&nbsp;нас не&nbsp;появилось. Наш образ жизни почти не&nbsp;изменился, а&nbsp;если и&nbsp;изменился, то&nbsp;не&nbsp;сильно.</p>
          <p>23&nbsp;марта я&nbsp;отмечала свой день рождения. В&nbsp;тот момент ещё все могли спокойно выходить на&nbsp;улицу и&nbsp;общаться, и&nbsp;я&nbsp;смогла отметить свой день рождения в&nbsp;компании близких мне людей.</p>
          <p>К&nbsp;сожалению, дистанционное обучение&nbsp;&mdash; весьма странная форма обучения для меня, она сама является одновременно и&nbsp;плюсом и&nbsp;минусом пандемии.</p>
          <p>Во&nbsp;время наших уроков не&nbsp;происходит ничего особенного, мы&nbsp;выпускной класс и&nbsp;учимся и&nbsp;готовимся к&nbsp;сдаче экзаменов. По&nbsp;моему мнению, учителя не&nbsp;стали строже, скорее наоборот. Нам дают большие временные рамки на&nbsp;написание работ, и, если вдруг не&nbsp;успел, то&nbsp;дают отсрочку. Получение хороших оценок стало проще, но&nbsp;это связано не&nbsp;с&nbsp;упрощением заданий, а&nbsp;с&nbsp;тем, что появилось больше возможностей пользоваться дополнительными ресурсами при написании работы. Также работ стало меньше и&nbsp;оценок тоже, тем самым можно отлично написать работы, а&nbsp;их&nbsp;обычно по&nbsp;предмету не&nbsp;больше двух, и&nbsp;получить хорошую отметку в&nbsp;журнал.</p>
          <p>Мне кажется, что у&nbsp;меня уже слишком много планов на&nbsp;послекарантинное время. Скорее всего, я&nbsp;пойду гулять по&nbsp;своему району, возможно, в&nbsp;какой-то парк со&nbsp;своими друзьями и&nbsp;наконец встречусь со&nbsp;всеми, кем смогу. Мне поначалу будет страшно ездить на&nbsp;метро и&nbsp;общественном транспорте, поэтому я&nbsp;буду максимально избегать массовых скоплений и&nbsp;проводить время в&nbsp;комфортной мне обстановке. Я&nbsp;планирую гулять весь день. Для меня каждая прогулка&nbsp;&mdash; это что-то особенное и&nbsp;неповторимое, запоминающееся на&nbsp;долгое время.</p>
          <p>В&nbsp;мире много мест, в&nbsp;которых я&nbsp;хочу побывать. Например, мне&nbsp;бы хотелось посетить Нидерланды, США, съездить на&nbsp;Камчатку и&nbsp;на&nbsp;Байкал. Этим летом мы&nbsp;должны были лететь в&nbsp;Англию вместе с&nbsp;группой ребят из&nbsp;нашей школы, но, к&nbsp;сожалению, эта поездка отменилась. У&nbsp;меня с&nbsp;самого детства была мечта посетить США: Лос-Анджелес, Нью-Йорк, Чикаго, Вашингтон и&nbsp;Майами. Мы&nbsp;с&nbsp;мамой собирались осуществить эту мою мечту на&nbsp;мое пятнадцатилетие, на&nbsp;окончание 9-ого класса и&nbsp;вдвоем посетить эту страну. Наша поездка не&nbsp;состоится, и&nbsp;не&nbsp;знаю, произойдет&nbsp;ли когда-либо, но&nbsp;я&nbsp;на&nbsp;это очень надеюсь.</p>
          <p>Я&nbsp;не&nbsp;думаю, что что-то в&nbsp;мире после пандемии изменится кардинально. Возможно, люди станут больше ценить свободу, свои возможности и&nbsp;использовать&nbsp;их, станут более трепетно относиться к&nbsp;своей жизни и&nbsp;ценить все, что им&nbsp;дано. Возможно, люди начнут расставлять по-другому приоритеты в&nbsp;работе, учебе и&nbsp;увлечениях, потому что смогут понять, что время не&nbsp;вечно и&nbsp;нужно жить одним днём и&nbsp;выжимать из&nbsp;него по&nbsp;максимуму. Может быть, экологическое положение начнет улучшаться, и&nbsp;мы&nbsp;хоть чуть-чуть исправим катастрофическую ситуацию на&nbsp;планете. Например, за&nbsp;последние месяцы в&nbsp;городах воздух из-за снижения выбросов СО2 стал чище, что безусловно радует.</p>
      </div>
    </article>
  </Layout>
)

export default IndexPage



